<template>
  <nav class="navbar navbar-expand-lg navbar-dark bg-primary" style="padding: 0.5rem 1rem;">
    <router-link class="navbar-brand" :to="homeUrl">EtherLottery <sup data-toggle="tooltip" title="Kovan Test Network"><small>Kovan</small></sup></router-link>
    <ul class="navbar-nav mr-auto">
      <li class="nav-item">
        <router-link class="nav-link" to="/guide">{{ $t("Guide") }}</router-link>
      </li>
    </ul>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsibleNavbar">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="collapsibleNavbar">
      <ul class="navbar-nav ml-auto">
        <li class="nav-item">
          <a class="nav-link">{{ $t("Wallet") }}:{{ wallet }}</a>
        </li>
        <li class="nav-item dropdown">
          <a class="nav-link dropdown-toggle" href="#" data-toggle="dropdown">
            {{ $t("Contract") }}
          </a>
          <div class="dropdown-menu">
            <a class="dropdown-item" target="_lottery" :href="LotteryContractUrl">Lottery</a>
            <a class="dropdown-item" target="_yht" :href="YHTContractCodeUrl">YHT</a>
            <div class="dropdown-divider"></div>
            <a class="dropdown-item" target="_whitepage" :href="whitepaperUrl"> {{ $t("Whitepaper") }}</a>
          </div>
        </li>
        <li class="nav-item dropdown">
          <a class="nav-link dropdown-toggle" href="#" data-toggle="dropdown">
            {{ translations }}
          </a>
          <div class="dropdown-menu">
            <a class="dropdown-item" href="#" v-for="item in supportLanguages" :key="item[0]" @click="languageSwitch(item[0])">{{ item[1] }}</a>
          </div>
        </li>
      </ul>
    </div> 
  </nav>
</template>

<script>
import { mapState, mapGetters } from 'vuex'

export default {
  name: "Navbar",
  computed: {
    ...mapState({
      lang: 'lang',
      whitepaperUrl: 'whitepaperUrl',
      referrerParamId: 'referrerParamId',
      YHTContractCodeUrl: 'YHTContractCodeUrl',
      LotteryContractUrl: 'LotteryContractUrl'
    }),
    ...mapGetters([
      'wallet'
    ]),
    translations () {
      let item = i18n.supportLanguages.find(i => i[0] == this.lang);
      return item[2];
    },
    supportLanguages () {
      return i18n.supportLanguages.filter(i => i[0] != this.lang);
    },
    homeUrl () {
      let referrerParamId = this.referrerParamId;
      return referrerParamId ? `/referrer/${referrerParamId}` : '/';
    }
  },
  methods: {
    languageSwitch (lang) {
      i18n.load(lang);
    }
  }
}
</script>