import Vue from 'vue'
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)

const loadedLanguages = [];
const kDefaultLanguage = 'en';
const kStorageKey = 'Language';

const supportLanguages = [
  ['en', 'English', 'Translations'],
  ['zh', '中文', '多语言'],
  ['ja', '日本語', '言語'],
  ['ko', '중국어', '다른 언어로 보기'],
  ['ru', 'Русский', 'Переводы']
];

const i18n = new VueI18n({
  fallbackLocale: kDefaultLanguage,
  silentTranslationWarn: true,
});

function getLangData(lang) {
  return import(/* webpackChunkName: "lang-[request]" */ `@/lang/${lang}`);
}

async function load(lang) {
  if (!lang) {
    lang = localStorage.getItem(kStorageKey);
    if (!lang) {
      lang = navigator.language || kDefaultLanguage; 
    }
  }
  if (i18n.locale !== lang) {
    if (!loadedLanguages.includes(lang)) {
      let data;
      try {
        data = await getLangData(lang);
      } catch (error) {
        console.warn(error);
        let i = lang.indexOf("-");
        if (i != -1) {
          lang = lang.substring(0, i);
          try {
            data = await getLangData(lang);
          } 
          catch (error) {
            console.warn(error);
            lang = kDefaultLanguage;
            data = await getLangData(lang);
          }
        }
        else {
          lang = kDefaultLanguage;
          data = await getLangData(lang);
        }
      }
      i18n.setLocaleMessage(lang, data.default);
      loadedLanguages.push(lang)
    }
    i18n.locale = lang;
    document.querySelector('html').setAttribute('lang', lang);
    localStorage.setItem(kStorageKey, lang);
    window.dispatchEvent(new CustomEvent('locale', { detail: lang }));
  }
  return lang;
}

i18n.supportLanguages = supportLanguages;
i18n.load = load;
export default i18n;