import Vue from 'vue'
import VueRouter from 'vue-router'

import './assets/Utils.js'
import i18n from './assets/i18n-setup.js'
import ContractDataProvider from './assets/DataProvider'

import App from './App.vue'
import Lottery from './components/Lottery.vue'
import Guide from './components/Guide.vue'

Vue.use(VueRouter);
const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/', 
      component: Lottery
    },
    {
      path: '/referrer/:referrerId',
      component: Lottery
    },
    {
      path: '/guide',
      component: Guide
    },
    {
      path: '/*', 
      redirect: '/'
    }
  ]
});
router.afterEach((to, from) => {
  ga('set', 'page', to.path);
  ga('send', 'pageview');
});

Vue.config.productionTip = false

i18n.load();
window.i18n = i18n;
let dataProvider = new ContractDataProvider();
window.dataProvider = dataProvider;

dataProvider.start(() => {
  new Vue({
    i18n,
    router,
    store: dataProvider.store,
    render: h => h(App)
  }).$mount('#app');
  $('#loading').remove();
});

$(function () {
  window.ga = window.ga || function() {
    (window.ga.q = window.ga.q || []).push(arguments)
  }
  ga('create', 'UA-124896241-1', 'auto');   // create a new tracker object
  ga('set', 'transport', 'beacon');   // Updates the tracker to use `navigator.sendBeacon` if available.
});