import Vue from 'vue'
import Vuex from 'vuex'

import YHTABI from './YHTABI.js'
import LotteryABI from './LotteryABI.js'

const getNetworkFromUrl = function (infuraUrl) {
  let begin = infuraUrl.indexOf('//');
  console.assert(begin != -1);
  begin += 2;
  let end = infuraUrl.indexOf('.', begin);
  console.assert(end != -1);
  return infuraUrl.substring(begin, end);
};

const kEndpointUrl = "wss://kovan.infura.io/ws";
const kYHTAddress = "0x1e55ba990ead1a495b0218a7110aadb257054ca0";
const kLotteryAddrss = "0xe8b92041206ed1d983c44fbbf52bc87b2791411d";
const kWeiToEther = new BigNumber("1e+18");
const kMintTotalPerCycle = 271828.182845904523536028;
const kMintHalveCycleCount = 314;

const kNetWork = getNetworkFromUrl(kEndpointUrl);
const kNetWorkIds = { mainnet: 1, ropsten:3, rinkeby: 4, kovan: 42 };
const kNetId = kNetWorkIds[kNetWork];
const kHasWalletUseNewApi = false;

const kWhitepaperDoamin = 'https://etherlottery.xyz/whitepaper/';
const kWhitepaperDefault = kWhitepaperDoamin + 'en.pdf';

const getCodeAddressUrl = function (contractAddress) {
  if (kNetWork === 'mainnet') {
    return `https://etherscan.io/address/${contractAddress}#code`
  }
  else {
    return `https://${kNetWork}.etherscan.io/address/${contractAddress}#code`
  }
}

const getMintTotalCount = function (cycleCount) {
  let times =  ((cycleCount - 1) / kMintHalveCycleCount) >> 0;
  let total = (kMintTotalPerCycle / Math.pow(2, times));
  if (total < 1) {
    total = 0;
  }
  return total;
};

Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    YHTContractAddress: kYHTAddress,
    LotteryContractAddress: kLotteryAddrss,
    YHTContractCodeUrl: getCodeAddressUrl(kYHTAddress),
    LotteryContractUrl: getCodeAddressUrl(kLotteryAddrss),
    lang: '',
    whitepaperUrl: '',
    walletAddress: '',
    cycleCount: 0,
    nextOpenRewardTime: 0,
    initAmount: 0,
    betAmount: 0,
    betAddrsCount: 0,
    betCount: 0,
    betValue: 0,
    YHT: {
      totalSupply: 0,
      balance: 0,
      earnings: 0
    },
    winners: [],
    defaultBetAmount: 0.01,
    referrerParamId: 0,
    referrerCount: 0,
    referrerId: 0,
    bindReferrerId: 0,
    bindCycleCount: 0,
    beBindCount: 0,
  },
  mutations: {
    lang (state, language) {
      state.lang = language;
    },
    whitepaperUrl (state, whitepaperUrl) {
      state.whitepaperUrl = whitepaperUrl;
    },
    referrerParamId (state, id) {
      state.referrerParamId = id;
    },
    walletAddress (state, address) {
      state.walletAddress = address;
    },
    initBetData (state, payload) {
      state.cycleCount = payload.cycleCount;
      state.nextOpenRewardTime = payload.nextOpenRewardTime;
      state.initAmount = payload.initAmount;
      state.betAmount = payload.betAmount;
      state.betAddrsCount = payload.betAddrsCount;
      state.betCount = payload.betCount;
      state.betValue = payload.betValue;
    },
    initReferrerData (state, payload) {
      state.referrerIdCounter = payload.referrerIdCounter;
      state.referrerId = payload.referrerId;
      state.bindReferrerId = payload.bindReferrerId;
      state.bindCycleCount = payload.bindCycleCount;
      state.beBindCount = payload.beBindCount;
    },
    updateBetAmount (state, payload) {
      state.initAmount = payload.initAmount;
      state.betAmount = payload.betAmount;
    },
    updateBetData (state, payload) {
      state.betAmount = payload.betAmount;
      state.betAddrsCount = payload.betAddrsCount;
      state.betCount = payload.betCount;
      state.betValue = payload.betValue;
    },
    initYHTData (state, payload) {
      state.YHT.totalSupply = payload.totalSupply;
      state.YHT.balance = payload.balance;
      state.YHT.earnings = payload.earnings;
    },
    clearEarnings (state) {
      state.YHT.earnings = 0;
    },
    addWinner (state, winner) {
      state.winners.push(winner);
    }
  },
  getters: {
    wallet (state) {
      let address = state.walletAddress;
      if (!address) {
        return i18n.t("walletDisconnect");
      }
      return address;
    },
    winningProbability (state) {
      if (!state.betValue) {
        return 0;
      }
      let rate = state.betValue / state.betAmount * 100;
      return rate.toFixed(2);
    },
    expectedIncome (state) {
      if (!state.betValue) {
        return 0;
      }
      let total = getMintTotalCount(state.cycleCount);
      let count = state.betValue / state.betAmount * total;
      return count.toFixed(3); 
    }, 
    remainReferrerCount (state) {
      return state.bindCycleCount + 15 - state.cycleCount;
    }
  }
}); 

window.addEventListener('locale', e => { 
  const lang = e.detail;
  store.commit('lang', lang);
  const kDomain = 'https://raw.githubusercontent.com/ethergame/whitepaper/master/docs/';
  const kFile = lang + '.pdf';
  $.ajax({
    url: kDomain + kFile,
    type:'HEAD',
    error: function () {
      store.commit('whitepaperUrl', kWhitepaperDefault);
    },
    success: function ()  {
      store.commit('whitepaperUrl', kWhitepaperDoamin + kFile);
    }
  });
});

const parseBalance = function (value) {
  let number = new BigNumber(value);
  return number.div(kWeiToEther).toNumber();    
}

const transactionHashs = {};
const checkTransactionHash = function (transactionHash) {
  if (!transactionHashs[transactionHash]) {
    transactionHashs[transactionHash] = true;
    return true;
  }  
  return false;
}

const checkEvent = function (event) {
  return checkTransactionHash(event.transactionHash + event.logIndex);
}

//https://italonascimento.github.io/applying-a-timeout-to-your-promises/
const promiseTimeout = function(ms, promise) {
  let timeoutId;
  // Create a promise that rejects in <ms> milliseconds
  let timeout = new Promise((resolve, reject) => {
    timeoutId = setTimeout(() => {
      reject('Timed out in '+ ms + 'ms.')
    }, ms)
  });
  promise.then(() => clearTimeout(timeoutId));
  // Returns a race between our timeout and the passed in promise
  return Promise.race([
    promise,
    timeout
  ]);
}

const getNetwork = function (web3) {
  return new Promise((resolve, reject) => {
    web3.version.getNetwork((error, result) => {
      if (error) {
        reject(error);
      }
      else {
        resolve(result);
      }
    });
  });
}

class NewWeb3Adapter {
  constructor(provider) {
    const convetData = function (obj) {
      if (obj instanceof Array) {
        const array = [];
        for (let i = 0; i < obj.length; ++i) {
          array.push(convetData(obj[i]));
        }
        return array;
      }
      else if (obj instanceof Object) {
        if (obj.abs) {
          return obj.toString();
        }
        const newObj = {};
        Object.keys(obj).forEach(key => {
          newObj[key] = convetData(obj[key]);
        });
        return newObj;
      }
      return obj.toString();
    }
    const convertEventResult = function (result) {
      if (result) {
        result.returnValues = convetData(result.args);
      }
      return result;
    };
    let web3 = new window.Web3(provider);
    class Contract {
      constructor (abi, address) {
        let contract = web3.eth.contract(abi).at(address);
        this.methods = {
          getPlayerInfos (playerAddress) {
            return {
              call () {
                return new Promise((resolve, reject) => {
                  contract.getPlayerInfos(playerAddress, (error, result) => {
                    if (error) {
                      reject(error);
                    }
                    else {
                      resolve(convetData(result));
                    }
                  });
                });
              }
            }
          },
          getUserInfos (userAddress) {
            return {
              call () {
                return new Promise((resolve, reject) => {
                  contract.getUserInfos(userAddress, (error, result) => {
                    if (error) {
                      reject(error);
                    }
                    else {
                      resolve(convetData(result));
                    }
                  });
                });
              }
            }
          },
          betWithReferrer (referrerId) {
            return {
              send (options, callback) {
                return new Promise((resolve, reject) => {
                  contract.betWithReferrer(referrerId, options, (error, result) => {
                    if (callback) {
                      callback(error, result);
                    }
                    if (error) {
                      reject(error);
                    }
                    else {
                      resolve(result);
                    }
                  });
                });
              }
            };
          },
          withdraw () {
            return {
              send (options, callback) {
                return new Promise((resolve, reject) => {
                  contract.withdraw(options, (error, result) => {
                    if (callback) {
                      callback(error, result);
                    }
                    if (error) {
                      reject(error);
                    }
                    else {
                      resolve(result);
                    }
                  });
                });
              }
            }
          }
        };
        this.events = {
          NewBet (callback) {
            contract.NewBet((error, result) => {
              callback(error, convertEventResult(result));
            });
          },
          LuckyMan (options, callback) {
            contract.LuckyMan(null, options, (error, result) => {
              callback(error, convertEventResult(result));
            });
          },
          CycleNew (callback) {
            contract.CycleNew((error, result) => {
              callback(error, convertEventResult(result));
            });
          },
          ObtainReferrerEarnings (callback) {
            contract.ObtainReferrerEarnings((error, result) => {
              callback(error, convertEventResult(result));
            });
          }
        }
      }
    }
    this.eth = {
      Contract,
      net: {
        getId () {
          return getNetwork(web3);
        }
      },
      getAccounts () {
        return new Promise(resolve => {
          resolve([ web3.eth.defaultAccount ]);
        });
      },
      getBlock (blockNumber) {
        return new Promise((resolve, reject) => {
          web3.eth.getBlock(blockNumber, (error, result) => {
            if (error) {
              reject(error);
            }
            else {
              resolve(result);
            }
          });
        });
      },
      sendTransaction (transactionObject, callback) {
        return new Promise((resolve, reject) => {
          web3.eth.sendTransaction(transactionObject, (error, result) => {
            if (callback) {
              callback(error, result);
            }
            if (error) {
              reject(error);
            }
            else {
              resolve(result);
            }
          });
        });
      }
    };
  }
}

let Web3Class;

const injectionNewWeb3 = function () {
  require('./libs/web3');
  Web3Class = window.Web3;
}

const injectWalletWeb3 = function () {
  Web3Class = NewWeb3Adapter;
}

firebase.initializeApp({
  apiKey: "AIzaSyCDIIZ7VIpYW2ElSIG8nLsnjwzKWamtlHM",
  authDomain: "etherlottery.firebaseapp.com",
  databaseURL: "https://etherlottery.firebaseio.com",
  projectId: "etherlottery",
  storageBucket: "",
  messagingSenderId: "59172838528"
});
const db = firebase.firestore();
db.settings({ timestampsInSnapshots: true });

class DataProvider {
  constructor() {
    this.store = store;
  }

  async connect() {
    if (!kHasWalletUseNewApi && this.wallet) {
      this.web3 = this.wallet.web3;
    }
    else {
      this.web3 = new Web3Class();
      let provider = new Web3Class.providers.WebsocketProvider(kEndpointUrl);
      this.web3.setProvider(provider);
      provider.on('error', e => console.log('WS Error', e));
      provider.on('end', async e => {
        console.log('WS closed', e);
        console.log('Attempting to reconnect...');
        await this.connect();
      });
      console.log('connect to', kEndpointUrl);
    }
    await this.init();
  }

  initContracts() {
    this.YHT = new this.web3.eth.Contract(YHTABI, kYHTAddress);
    this.Lottery =  new this.web3.eth.Contract(LotteryABI, kLotteryAddrss);
  }

  async getContractData() {
    let walletAddress = this.wallet ? this.wallet.address || '' : '';
    const [lastBlock, playerInfos, userInfos] = await Promise.all([
      this.web3.eth.getBlock("latest"),
      this.Lottery.methods.getPlayerInfos(walletAddress).call(),
      this.YHT.methods.getUserInfos(walletAddress).call()
    ]);
    let data = {
      lastBlock,
      playerInfos,
      userInfos,
    };
    console.log(data);
    return data;
  }

  async refreshData() {
    let contractData  = await this.getContractData();
    this.beginBlock = contractData.lastBlock;
    this.loadDatasTime = new Date();

    let playerInfos = contractData.playerInfos;
    let betInfos = playerInfos[0];
    let cycleCount = parseInt(betInfos[0]);
    let nextOpenRewardTime = parseInt(betInfos[1]);
    let initAmount = parseBalance(betInfos[2]);
    let betAmount = parseBalance(betInfos[3]);
    let betAddrsCount = parseInt(betInfos[4]);
    let betCount = parseInt(betInfos[5]);
    let betValue = parseBalance(betInfos[6]);
    store.commit("initBetData", { cycleCount, nextOpenRewardTime, initAmount, betAmount, betAddrsCount, betCount, betValue });

    let referrerInfos = playerInfos[1];
    let referrerIdCounter = parseInt(referrerInfos[0]);
    let referrerId = parseInt(referrerInfos[1]);
    let bindReferrerId = parseInt(referrerInfos[2]);
    let bindCycleCount = parseInt(referrerInfos[3]);
    let beBindCount = parseInt(referrerInfos[4]);
    store.commit("initReferrerData", { referrerIdCounter, referrerId, bindReferrerId, bindCycleCount, beBindCount });

    let totalSupply = parseBalance(contractData.userInfos[0]);
    let balance = parseBalance(contractData.userInfos[1]);
    let earnings = parseBalance(contractData.userInfos[2]);
    store.commit("initYHTData", { totalSupply, balance, earnings });
  }

  registerEvents() {
    this.Lottery.events.CycleNew((error, event) => {
      if (!error) {
        if (checkEvent(event)) {
          this.refreshData();
        }
      }
      else {
        console.error(error);
      }
    });

    this.Lottery.events.NewBet((error, event) => {
      if (!error) {
        if (checkEvent(event)) {
          let returnValues = event.returnValues;
          let betAmount = parseBalance(returnValues.betAmount);
          let betAddrsCount = parseInt(returnValues.betAddrsCount);
          let betCount = parseInt(returnValues.betCount);
          let betValue = 0;
          if (this.wallet && returnValues.playerAddress == this.wallet.address) {
            betValue = parseBalance(returnValues.betValue);
          }
          store.commit("updateBetData", { betAmount, betAddrsCount, betCount, betValue });
          window.dispatchEvent(new CustomEvent("NewBet", {
            detail: {
              playerAddress: returnValues.playerAddress,
              value: parseBalance(returnValues.value)
            }
          }));
          if (betValue > 0) {
            if (store.state.referrerId == 0) {
              this.refreshData();
            }
          }
        }
      }
      else {
        console.error(error);
      }
    });

    this.Lottery.events.LuckyMan({ fromBlock: 0 }, (error, event) => {
      if (!error) {
        if (checkEvent(event)) {
          let returnValues = event.returnValues;   
          let item = {
            cycleCount: parseInt(returnValues.cycleCount),
            openRewardTime: parseInt(returnValues.openRewardTime),
            playerAddress:  returnValues.playerAddress,
            betValue: parseBalance(returnValues.betValue),
            reward: parseBalance(returnValues.reward)
          };
          store.commit("addWinner", item);
          if (event.blockNumber > this.beginBlock.number) {
            window.dispatchEvent(new CustomEvent("LuckyMan", { detail: item }));
          }
        }
      }
      else {
        console.error(error);
      }
    });

    this.Lottery.events.ObtainReferrerEarnings((error, event) => {
      if (!error) {
        if (checkEvent(event)) {
          let returnValues = event.returnValues;
          if (this.wallet && returnValues.referrerAdress == this.wallet.address) {
            let beBindCount = parseInt(returnValues.beBindCount);
            store.commit("beBindCount", beBindCount);
          }
        }
      }
      else {
        console.error(error);
      }
    });
  }

  async init() {
    this.initContracts();
    await this.refreshData();
    this.registerEvents();
  }

  async getWalletAccount(web3) {
    const accounts = await web3.eth.getAccounts();
    return accounts[0];
  }

  async start(callback) {
    if (window.web3) {
      const oldWeb3 = new Web3(window.web3.currentProvider);
      let netId;
      try {
        netId = await promiseTimeout(6000, getNetwork(oldWeb3));
      }  catch (e) {
        console.error(e);
      }
      if (window.imToken || netId == kNetId) {
        if (kHasWalletUseNewApi) {
          injectionNewWeb3();
        }
        else {
          injectWalletWeb3();
        }
        try {
          let web3 = new Web3Class(window.web3.currentProvider); 
          const account = await this.getWalletAccount(web3);
          this.wallet = {
            web3: web3,
            netId: netId,
            address: account,
            YHT: new web3.eth.Contract(YHTABI, kYHTAddress),
            Lottery: new web3.eth.Contract(LotteryABI, kLotteryAddrss)
          };
          store.commit("walletAddress",  this.wallet.address);
        } catch (error) {
          console.log(error);
        }
      }
      else {
        injectionNewWeb3();
      }
    }
    else {
      injectionNewWeb3();
    }

    await this.connect();
    if (this.wallet) {
      setInterval(async () => {
        let accounts = await this.wallet.web3.eth.getAccounts();
        if (accounts[0] != this.wallet.address) {
          this.wallet.address = accounts[0];
          store.commit("walletAddress",  this.wallet.address);
          this.refreshData();
        }
      }, 1000);
    }
    callback();
  }

  isWalletEnable() {
    return this.wallet && this.wallet.address && this.wallet.netId == kNetId;
  }

  isWalletExists() {
    return this.wallet && this.wallet.address;
  }

  getOpenRewardRemainTime() {
    let now = new Date();
    let past = ((now.getTime() - this.loadDatasTime.getTime()) / 1000) >> 0; 
    return store.state.nextOpenRewardTime - this.beginBlock.timestamp - past; 
  }

  gaLog(category, action, label, value) {
    ga('send', 'event', category, action, label, value, {
      'hitCallback': function() {
        console.debug('ga send event success', category, action, label, value);
      }
    });
  }

  async betDataToDb(transactionHash, value) {
    const ip = await $.get('https://api.ipify.org');
    db.collection("bets").add({
      cycleCount: store.state.cycleCount,
      wallet: this.wallet.address,
      value: value,
      transactionHash: transactionHash,
      time: firebase.firestore.FieldValue.serverTimestamp(),
      ip: ip,  
      extras: {
        nextOpenRewardTime: new firebase.firestore.Timestamp(store.state.nextOpenRewardTime),
        betValue: store.state.betValue,
        initAmount: store.state.initAmount,
        betAmount: store.state.betAmount,
        betAddrsCount: store.state.betAddrsCount,
        betCount: store.state.betCount
      }
    }).then(function(docRef) {
      console.log("bet document written with ID: ", docRef.id);
    }).catch(function(error) {
      console.error("bet document error adding document: ", error);
    });
  }
  
  bet(value) {
    console.assert(this.isWalletExists(), "bet:not enable wallet");    
    this.wallet.Lottery.methods.betWithReferrer(store.state.referrerParamId).send({
      from: this.wallet.address,
      value: kWeiToEther.times(value)
    }, (error, result) => {
      if (error) {
        console.error(error);
        this.gaLog('bet', 'failed');
      } else {
        this.betDataToDb(result, value);
        console.log('bet', result);
        this.gaLog('bet', 'success', value.toString());
      }
    });
    this.gaLog('bet', 'try');
  }

  withdraw() {
    console.assert(this.isWalletExists(), "withdraw:not enable wallet");   
    this.wallet.YHT.methods.withdraw().send({ 
      from: this.wallet.address 
    }, (error, result) => {
      if (error) {
        console.log(error);
        this.gaLog('withdraw', 'failed');
      } else {
        console.log('withdraw', result);
        store.commit("clearEarnings");
        this.gaLog('withdraw', 'success');
      }
    });
    this.gaLog('withdraw', 'try');
  }
}

export default DataProvider;