<template>
  <div>
    <div class="container jumbotron bg-none">
      <div class="text-center">
        <h5 class="display-5">
          <a class="space-not-one">#{{ cycleCount }}  {{ $t("IssueDate", [nextOpenRewardTime.toDateString()]) }}</a>  
          <a v-if="isRemainTime" class="space-not-one">  {{ $t("Time Left")}} {{ remainTime }}</a>
          <span data-toggle="tooltip" data-placement="right" :title="$t('TimeoutTips')">
             <a v-if="!isRemainTime" class="space-not-one">  {{ $t("Settlement Timeout") }} {{ remainTime }}</a>
          </span>
        </h5>
        <div>
          <button type="button" class="btn bg-none btn-total" data-toggle="popover" data-html="true" data-placement="right" :data-content="$t('PoolSource', [initAmount.toFixed(5), betAmount.toFixed(5)])">
            <h1 class="display-1">{{ animatedTotal }} ETH</h1>
          </button>
        </div>
        <small>{{ $t("PlayersAndBets", [betAddrsCount, betCount]) }}</small>
      </div>
      <div class="row pt-3">
        <div class="col-sm-9 col-md-6 offset-md-3">
          <form>
            <div class="input-group">
               <div class="input-group-prepend">
                <span class="input-group-text"><i class="fab fa-ethereum"></i></span>
              </div>
              <input type="text" class="form-control" :placeholder="defaultBet" v-model.number="betInputValue">
              <div class="input-group-append pr-2">
                <button type="button" class="btn btn-secondary" @click="bet">{{ $t("Bet") }}</button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>

    <div class="container mb-4">
      <div class="card bg-none">
        <div class="card-header">{{ $t("Wallet") }}:{{ wallet }}</div>
        <div class="card-body">
          <table class="table">
            <tbody>
              <tr>
                <td>{{ $t("Current Bet") }}</td>
                <td>
                  {{ betValue }} ETH
                  <div>
                    <small>{{ $t("Winning Probability") }} {{ winningProbability }}%,  {{ $t("Will Get") }} {{ expectedIncome }} YHT</small>
                  </div>            
                </td>
              </tr>
            </tbody>
            <tbody>
              <tr>
                <td>{{ $t("Holding YHT Balance") }}</td>
                <td>
                  {{ YHT.balance }} YHT
                  <div>
                    <small>{{ $t("YHT Total Supply") }} {{ YHT.totalSupply }}</small>
                  </div>
                </td>
              </tr>
            </tbody>
            <tbody>
              <tr>
                <td>{{ $t("Earnings") }}</td>
                <td>{{ YHT.earnings }} ETH</td>
                <td v-if="walletAddress">
                  <button type="button" class="btn btn-secondary" @click="withdraw">{{ $t("Withdraw") }}</button>
                </td>
              </tr>
            </tbody>
            <tbody v-if="referrerId">
              <tr>
               <td>
                {{ $t("Affiliate Link") }} 
                <router-link class="ml-2" to="/guide#affiliate">
                  <i class="fa fa-question-circle" style="" aria-hidden="true"></i>
                </router-link>
               </td>
               <td>
                {{ ReferrerUrl }}
                <div v-if="beBindCount">
                  <small>{{ $t("Referrals", [beBindCount]) }}</small>
                </div>
                <div v-if="bindReferrerId">
                  <small>{{ $t("BindReferrerId") }} {{ bindReferrerId }}</small>
                </div>
               </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <div class="container">
      <div class="card bg-none">
        <div class="card-header">{{ $t("Winner Information") }}</div>
        <div class="card-body">
          <table class="table table-responsive-sm">
              <thead>
                <tr>
                  <th>{{ $t("Issue") }}</th>
                  <th>{{ $t("Address") }}</th>
                  <th>{{ $t("Bet Amount") }}</th>
                  <th>{{ $t("Earnings") }}</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in lastWinners" :key="item.cycleCount">
                  <td class="space-not-one">#{{ item.cycleCount }}  {{ item.openRewardTime.toDateString() }}</td>
                  <td>{{ item.playerAddress }}</td>
                  <td>{{ item.betValue }}</td>
                  <td>{{ item.reward }}</td>
                </tr>
              </tbody>
          </table>
          <ul class="pagination">
            <li class="page-item"><a class="page-link" @click="winnerPageSwitch(false)">{{ $t("Previous") }}</a></li>
            <li class="page-item disabled"><a class="page-link">{{ winnerPage.index + 1 }}/{{ winnerPageCount }}</a></li>
            <li class="page-item"><a class="page-link" @click="winnerPageSwitch(true)">{{ $t("Next") }}</a></li>
          </ul>
        </div> 
      </div>
    </div>

    <div class="modal fade" id="betModal" role="dialog" aria-labelledby="betModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-dialog-center">
        <div class="modal-content">
          <div class="modal-header">
            <h4 class="modal-title" id="betModalLabel">{{ $t("Make Bet") }}</h4>
            <button type="button" class="close" data-dismiss="modal">&times;</button>
          </div>
          <div class="modal-body">
            <span>{{ LotteryContractAddress }}</span>
            <div id="lotteryAddressCode">
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">{{ $t("Close") }}</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="winnerModal">
      <div class="modal-dialog modal-lg modal-dialog-center">
        <div class="modal-content">
          <div class="modal-header">
            <h4 class="modal-title">{{ $t("Lucky Man") }}</h4>
            <button type="button" class="close" data-dismiss="modal">&times;</button>
          </div>
          <div class="modal-body text-center">
            <h1>{{ $t("Congratulations") }}</h1>
            <h2>{{ winner.address }}</h2>
            <h3>{{ $t("Gain") }}</h3>
            <h1>{{ winner.reward }} ETH</h1>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">{{ $t("Close") }}</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'Lottery',
  data () {
    return {
      nextOpenRewardRemainTime: dataProvider.getOpenRewardRemainTime(),
      tweenedTotal: 0,
      betInputValue: '',
      winnerPage: {
        index: 0,
        perCount: 5
      },
      winner: {
        address: '',
        reward: ''
      }
    };
  },
  created () {
    let initAmount = this.initAmount;
    let betAmount = this.betAmount;
    this.$store.commit("updateBetAmount", { initAmount: 0, betAmount: 0 });
    setTimeout(() => {
      this.$store.commit("updateBetAmount", { initAmount, betAmount });
    }, 100);
    
    const timerId = setInterval(() => {
        this.nextOpenRewardRemainTime = dataProvider.getOpenRewardRemainTime();
    }, 1000);
    const luckyManAction = e => {
      let data = e.detail;
      this.luckyMan(data.playerAddress, data.reward);
    };
    window.addEventListener('LuckyMan', luckyManAction);
    this.$once('hook:beforeDestroy', () => {
      clearInterval(timerId);
      window.removeEventListener('LuckyMan', luckyManAction);
      $('.popover').remove();
    });
  },
  mounted () {
    let referrerParamId =  parseInt(this.$route.params.referrerId);
    if (referrerParamId) {
       this.$store.commit('referrerParamId', referrerParamId);
    }
    $('[data-toggle="popover"]').popover(); 
    $('[data-toggle="tooltip"]').tooltip();   
  },
  computed: {
     ...mapState({
       LotteryContractAddress: 'LotteryContractAddress',
       walletAddress: 'walletAddress',
       cycleCount: 'cycleCount',
       nextOpenRewardTime: 'nextOpenRewardTime',
       initAmount: 'initAmount',
       betAmount: 'betAmount',
       betAddrsCount: 'betAddrsCount',
       betCount: 'betCount',
       betValue: 'betValue',
       YHT: 'YHT',
       winners: 'winners',
       defaultBet: 'defaultBetAmount',
       referrerId: 'referrerId',
       bindReferrerId: 'bindReferrerId',
       beBindCount: 'beBindCount',
     }),
     ...mapGetters([
       'wallet',
       'winningProbability',
       'expectedIncome',
       'remainReferrerCount',
     ]),
    total () {
      return (this.initAmount + this.betAmount).toFixed(4);
    },
    animatedTotal () {
      return this.tweenedTotal.toFixed(4); 
    },
    isRemainTime () {
      return this.nextOpenRewardRemainTime >= 0;
    },
    remainTime () {
      let remainTime = Math.abs(this.nextOpenRewardRemainTime);
      let hours = (remainTime / 3600) >> 0;
      let minutes = ((remainTime % 3600) / 60) >> 0;
      let seconds = (remainTime % 60) >> 0;
      return `${hours.toTimeStepString()}:${minutes.toTimeStepString()}:${seconds.toTimeStepString()}`;
    },
    lastWinners () {
      let winnerPage = this.winnerPage;
      let items = this.winners;
      let winners = [];
      let begin = items.length - 1 - winnerPage.index * winnerPage.perCount;
      if (begin < 0) {
        begin += winnerPage.perCount;
      }
      if (begin < items.length) {
        for (let i = begin; i >=0; --i) {
          winners.push(items[i]);
          if (winners.length == winnerPage.perCount) {
            break;
          }
        }
      }
      return winners;
    },
    winnerPageCount () {
      return Math.ceil(this.winners.length / this.winnerPage.perCount);
    },
    ReferrerUrl () {
      let location = window.location;
      return `${location.protocol}//${location.host}/referrer/${this.referrerId}`;
    }
  },
  watch: {
    total (newValue) {
      TweenLite.to(this.$data, 0.5, { tweenedTotal: newValue });
    }
  },
  methods: {
    winnerPageSwitch (next) {
      if (this.winnerPageCount > 0) {
        let pageIndex = this.winnerPage.index;
        if (next) {
          ++pageIndex;
        } else {
          --pageIndex;
        }
        if (pageIndex < 0) {
          pageIndex = 0;
        }
        else if (pageIndex > this.winnerPageCount - 1) {
          pageIndex = this.winnerPageCount - 1;
        }
        this.winnerPage.index = pageIndex;
      }
    },
    luckyMan (address, reward) {
      this.winner.address = address;
      this.winner.reward = reward;
      $("#winnerModal").modal();
    },
    bet () {
      if (dataProvider.isWalletEnable()) {
        let betValue = this.betInputValue;
        if (!betValue || typeof betValue != 'number') {
          this.betInputValue = '';
          betValue = this.defaultBet;
        }
        dataProvider.bet(betValue);
      }
      else {
        if (!this.lotteryAddressCode) {
          this.lotteryAddressCode = new QRCode("lotteryAddressCode", {
            text: this.LotteryContractAddress,
            correctLevel : QRCode.CorrectLevel.H
          });
        }
        $("#betModal").modal();
      }
    },
    withdraw () {
      dataProvider.withdraw();
    }
  }
}
</script>

